@import url('https://fonts.googleapis.com/css2?family=Source+Sans+Pro:ital,wght@0,200;0,300;0,400;0,600;0,700;0,900;1,200;1,300;1,400;1,600;1,700;1,900&display=swap');

* {
    font-family: 'Source Sans Pro', sans-serif;
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

body {
    font-size: 16px;
    background: #eeeeee;
}

a {
    text-decoration: none;
    color:aqua
}

ul,li {
    list-style-type: none;
}

input,textarea {
    padding:  10px;
    outline: none;
    border: none;
    background: none;
    width: 100%;
}
button {
    outline: none;
    border: none
}

i {
    margin-right: 5px;
}

.app {
    padding: 80px 0 40px 0;
}
.sign-up {
    background: #eeeeee;
    width: 100%;
    height: 100%;
    position: fixed;
    top:0;
    left:0;
    display: flex;
    justify-content: center;
    align-items:center;

    .sign {
        background: white;
        width: 80%;
        
        .title {

            padding: 10px;
            border-bottom: 1px solid silver;
            font-weight: 600;
        }

        .form {
            padding: 10px;
            overflow: auto;

            input {
                border: 1px solid silver;
                margin: 5px 0;
            }
            button {
                padding: 5px 10px;
                font-weight: 600;
                border: 1px solid silver;
                float: right
            }
        }
    }
}

.header {
    background: white;
    padding: 10px;
    display:flex;
    justify-content: space-between;
    width:100%;
    position: fixed;
    top:0;
    left:0;

    > div  {
        display:flex;
        justify-content: center;
        align-items: center;

        div {
            margin: 0 10px;
        }

        .menu {
            i {
                font-weight: bold;
            }
        }
        .logo {
            background: black;
            padding: 3px 8px;
            border-radius: 3px;
            color:white;
            font-weight: bold;
        }
    }
}

.footer {
    padding: 30px;
    background: white;
    text-align: center;
    font-size: 20px;
    font-weight: bold;
}

.menu-list {
    background: white;
    margin: 15px;

    .menu-list_title {
        padding: 10px;
        border-bottom: 1px solid black;
        font-weight: bold;
    }

    .menu-list_sub {
        .item {
            padding: 10px
        }
    }
}

.chat {
    ul {
        height: 300px;
        overflow-y: scroll;

        li {
            .user {
                margin-right:3px;
            }
            .content {
                margin-left: 3px;
            }
            &:not(:last-child){
                border-bottom: 1px solid silver;
            }
        }

    }
    .form {
        display: flex;
        align-items: center;
        border-top: 1px solid rgb(150, 144, 144);

        textarea {
            width: 100%;
            border: 1px solid silver
        }
        button {
            background: none;
            width: 100px;
            border-left: 1px solid silver;

            i {
                font-size:  25px;
                color: rgb(37, 118, 223)
            }
        }
    }
}